 @import "_variables.scss";

*{
  box-sizing: border-box;
}

img {
  display: inline-block;
}

body {
  font-size: $fz14;
}

::-webkit-scrollbar-track-piece{width:10px;background-color:#449ce4 ;  }

::-webkit-scrollbar{width:3px;height:6px ; }

::-webkit-scrollbar-thumb{height:50px;background:#075596;   cursor:pointer;}

::-webkit-scrollbar-thumb:hover{background:#075596; cursor:pointer; }

input {
  outline: none;
}

.relative {
  position: relative;
}

.c-dark {
  color: $dark;
}

.fz-12 {
  font-size: $fz12;
}

.fz-14 {
  font-size: $fz14;
}

.breeze-body,
.surface {
	width: 100%;
	height: 100%;
	overflow: hidden;
}

.surface {
	position: relative;
}

/* 左侧菜单 */
.surface-menu {
	position: absolute;
	left: 0;
  top: 0;
  z-index: 13;
	width: $widthNormal;
	height: 100%;
  @include light2bule;
  transition: width 300ms;

  // 窄的情况
  &.narrow {
    width: $widthNarrow;
    
    //效果不好，先注释
    .logo {
      width: $widthNarrow;
      overflow: hidden;
    }

    .sidebar-main {
      .text-name-contain {
        width: 0;
        padding: 0;
        opacity: 0;
        overflow: hidden;
      }
      .arrow-icon {
        display: none;
      }
    }

    .menu-btns {
      padding: 0;

      .icons-quick {
        margin-left: 5px;
        margin-right: 0;
      }

      .text-name {
        display: none;
      }
    }

  }
}

.logo {
  width: $widthNormal;
  height: 50px;
}

.search {
  padding: $pd-10;
}

.search-contain {
  height: 28px;
  line-height: 28px;
  border-radius: 14px;
  background-color: $dark-bule;
}

.icons-search {
  position: absolute;
  top: 5px;
  left: 10px;
  width: 18px;
  height: 18px;
  display: inline-block;
  cursor: pointer;
  background-image: url('images/search_white.png');
  background-repeat: no-repeat;
}

.search-input {
  border: none;
  height: 28px;
  line-height: 28px;
  width: 100%;
  padding-left: 35px;
  background-color: transparent;
  color: $white;
}

.menu-contain {
  position: absolute;
  top: 100px;
  left: 0;
  bottom: 32px;
  width: 100%;
}

// 一级菜单
.sidebar-main {
  width: 100%;
  height: 100%;
  overflow: auto;
  padding: 0 10px;
  .leaf-item {
    height: 38px;
    line-height: 38px;
    margin-bottom: 5px;
    
    a {
      display: block;
      color: $white;

      // 字体图标
      .font-icons {
        position: absolute;
        left: 8px;
        top: 8px;
        z-index: 10;
        margin-top: 0;
        width: 22px;
        height: 22px;
        line-height: 22px;
        text-align: center;
        border-radius: 50%;
        font-size: $fz14;
        background-color: $iconbule;
      }
      // 文字
      .text-name-contain {
        width: 100%;
        padding: 0 22px 0 38px;
        // transition: all 300ms;
      }
      // 更多图标
      .arrow-icon {
        position: absolute;
        top: 13px;
        right: 5px;
        z-index: 10;
        width: 12px;
        height: 12px;
        background-image: url('images/icon_more.png');
        background-repeat: no-repeat;
        background-position: center;
      }
    }

    &.active,
    &:hover {
      background-color: $greywhite;
      border-radius: 4px;
      .font-icons {
        color: $white;
      }
      a {
        color: $bule;
      }

      .arrow-icon {
        background-image: url('images/icon_more_hover.png');
      }
    }
  }
}

// 二级菜单
.sidebar-popup {
  position: absolute;
  top: 20px;
  left:  103%;
  z-index: 11;
  display: none;
  border-radius: 4px;
  background-color: $dark-bule;
  width: $widthNormal;
  padding: 0 10px;
  transition: top 150ms, bottom 150ms;

  .popup-name {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 12;
    padding: 0 10px;
    width: 100%;
    background-color: #3795e2;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }

  .popup-title {
    border-bottom: 1px solid #4ea1e6;
    color: $white;
    line-height: 38px;
  }
  .second-warp {
    height: 100%;
    padding-top: 39px;
    overflow-y: auto;

    // 一级
    &>ul>.leaf-item {
      // height: 38px;
      line-height: 38px;
      margin-bottom: 5px;
      
      // 二级
      .dep-1 {
        background-color: $lightbule;
        border-radius: 4px;

        .icons-contain {
          display: none;
        }

        .text-name-contain {
          padding-left: 30px;
        }
        
        //三级
        .dep-2 {
          .icons-contain {
            display: none;
          }

          .text-name-contain {
            padding-left: 38px;

            &:after {
              content: '▪';
              position: absolute;
              left: 16px;
              top: 8px;
              z-index: 10;
              width: 22px;
              height: 22px;
              line-height: 22px;
              text-align: center;
            }
          }
        }
      }

      ul {
        display: none;
      }

      a {
        display: block;
        color: $white;
        &:hover {
          background-color: $greywhite;
          color: $bule;
          border-radius: 2px;

          .arrow-icon {
            background-image: url('images/icon_more_hover.png');
          }
        }

        // 字体图标
        .font-icons {
          position: absolute;
          left: 8px;
          top: 8px;
          z-index: 10;
          margin-top: 0;
          width: 22px;
          height: 22px;
          line-height: 22px;
          text-align: center;
        }
        // 文字
        .text-name-contain {
          width: 100%;
          padding: 0 22px 0 38px;
        }
        // 更多图标
        .arrow-icon {
          position: absolute;
          top: 13px;
          right: 5px;
          z-index: 10;
          width: 12px;
          height: 12px;
          background-image: url('images/icon_more.png');
          background-repeat: no-repeat;
          background-position: center;
        }

        &.active {
          .arrow-icon {
            background-image: url('images/icon_more_down.png');
          }

          :hover {
            .arrow-icon {
              background-image: url('images/icon_more_down_hover.png');
            }
          }
        }
      }
    }
  }
  
}

.menu-btns {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 32px;
  width: 100%;
  padding: 0 10px;
  
  .icons-quick {
    width: 22px;
    height: 12px;
    display: inline-block;
    border-radius: 6px;
    background-color: rgba(0,0,0,.15);

    margin-top: 10px;
    margin-right: 10px;
    margin-left: 10px;
    transition: background-color 150ms;
    cursor: pointer;

    &:before {
      content: '';
      position: absolute;
      top: 1px;
      left: 1px;
      z-index: 10;
      width: 10px;
      height: 10px;
      border-radius: 5px;
      display: block;
      background-color: $white;
      transition: left 150ms;
    }

    &.active {
      background-color: rgba(47,118,200,1);

      &:before {
        left: 11px;
      }
    }
  }
  .text-name {
    color: $white;
    line-height: 32px;
  }

  .icons-trigger {
    background-image: url('images/outside.png');
    background-repeat: no-repeat;
    margin-top: 7px;
    cursor: pointer;
    &.active {
      background-image: url('images/inside.png');
    }
  }
}

/* 右侧功能区 */
.surface-main {
	 position: absolute;
	 left: $widthNormal;
	 top: 0;
	 right: 0;
   height: 100%;
   transition: left 300ms;
   @include bgwhite;

   &.narrow {
     left: $widthNarrow;
   }

   &.showtab {
     .surface-header {
      height: 70px;
      background-color: $bg-white;
      // box-shadow: 0 0 8px 1px rgba(102, 102, 102,.3); 

      .change-tab {
        background-image: url('images/tab-icon_active.png');
      }
      .tabs {
        display: block;
      }
     }
     .surface-content {
       top: 70px;
     }
   }
}

/* 右侧头部 */
.surface-header {
  height: 50px;
  padding-top: 10px;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
  background-color: $bg-lightbule;
  transition: background-color .5s cubic-bezier(0.22, 0.61, 0.36, 1),height .5s cubic-bezier(0.22, 0.61, 0.36, 1);;
}

/* 固定头部 */
.fix-header {
  height: 30px;
  line-height: 30px;
}

.icons {
  width: 18px;
  height: 18px;
  display: inline-block;
  margin-top: 6px;
  margin-right: 8px;
  cursor: pointer;
}

.change-tab {
  background-image: url('images/tab-icon.png');
  background-size: 100%;
  background-repeat: no-repeat;
}

.change-tab:hover {
  background-image: url('images/tab-icon_active.png');
}

.icons-emsg,
.icons-theme,
.icons-chat {
  margin-right: 20px;
}

.icons-emsg {
  background-image: url('images/right_icons_emsg.png');
}

.icons-theme {
  background-image: url('images/right_icons_theme.png');
}

.icons-chat {
  background-image: url('images/right_icons_chat.png');
}

.icons-user {
  // background-image: url('images/right_icons_user.png');
}

.head-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 18px;
  height: 18px;
}

.tip-num{
  position: absolute;
  top: -8px;
  left: 10px;
  z-index: 1;
  color: $white;
  background-color: $bg-red;
  text-align: center;
  height: 15px;
  min-width: 20px;
  line-height: 15px;
  border-radius: 8px;
} 

/* tab区域 */
.tabs {
  display: none;
  height: 30px;
  transition: height .5s cubic-bezier(0.22, 0.61, 0.36, 1);
  .fix-tab {
    width: 70px;
  }
  .flex-tab {
    position: absolute;
    top: 0;
    left: 70px;
    right: 0;
    height: 30px;
    line-height: 30px;
    background-color: $tmp;
  }
}

/* 右侧内容区 */
.surface-content {
  position: absolute;
  left: 0;
  top: 50px;
  bottom: 0;
  right: 0;
}

.page-cover {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 12;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0);
}

@import "_tabs.scss";
@import "_right.scss";
@import "_user.scss";

