.msg-panel {
  position: absolute;
  z-index: 14;
  top: 48px;
  right: -350px;
  bottom: 0;
  width: 350px;
  border-top: 2px solid #77abe4;
  border-left: 1px solid #dfdfdf;
  background-color: #fff;
  box-shadow: -2px 0 2px 0 rgba(0, 0, 0, .15);
}

.msg-panel-detail {
  position: relative;
  height: 100%;
}

.msg-panel-head {
  position: relative;
  padding-left: 10px;
  font-size: 16px;
  line-height: 36px;
  color: #2586d9;
  border-bottom: 1px solid #ddd;
  background-color: #f4f4f4;
}

.msg-panel-hide {
  position: absolute;
  top: 50%;
  right: 10px;
  width: 22px;
  height: 22px;
  margin-top: -11px;
  cursor: pointer;
  background: url('images/icon-toright.png');
}

.msg-panel-content,
.msg-panel-result,
.msg-panel-history {
  position: absolute;
  top: 38px;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
}

.msg-panel-content {
  background-color: #fff;
}

.msg-panel-content.empty {
  background: url('images/no-msg.png') no-repeat 50% 130px;
}
.msg-panel-content.msgorg {
  overflow: hidden;
}

.msg-panel-back {
  display: inline-block;
  width: 19px;
  height: 12px;
  margin-left: 10px;
  cursor: pointer;
  background: url('images/icon-back.png');
}

.msg-category {
  margin-bottom: 20px;
  margin-top: 10px;
  padding: 0 10px;
}

.msg-category-head,
.msg-result-head,
.msg-history-head {
  position: relative;
  height: 30px;
  border-bottom: 1px dashed #dcdcdc;
  background: url('images/msg-titleicon.png') no-repeat 0 7px;
}

.msg-result-head,
.msg-history-head {
  margin: 12px 10px 0;
}

.msg-category-title,
.msg-result-headertitle,
.msg-history-headertitle {
  margin-left: 10px;
  font-size: 14px;
  line-height: 30px;
  cursor: pointer;
  color: #0b3a51;
}

.msg-category-num {
  font-weight: normal;
  color: #f60;
}

.msg-category-remove {
  position: absolute;
  top: 50%;
  right: 4px;
  width: 15px;
  height: 18px;
  margin-top: -9px;
  cursor: pointer;
  background: url('images/icon-msgdel.png');
}

.msg-category-remove:hover {
  background: url('images/icon-msgdel-hover.png');
}

.msg-list,
.msg-srh-category {
  color: #555;
}

.msg-list {
  padding-left: 10px;
}

.msg-list-item {
  position: relative;
  line-height: 30px;
  cursor: pointer;
}

.msg-list-item:hover,
.msg-list-item.newmsg:hover {
  color: #f96d41;
}

.msg-item-text,
.msg-item-date {
  display: inline-block;
  vertical-align: top;
}

.msg-item-text {
  overflow: hidden;
  width: 245px;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.msg-item-text.warn1 {
  width: 220px;
  padding-left: 25px;
  background: url('images/icon-greenlight.png') left center no-repeat;
}

.msg-item-text.warn2 {
  width: 220px;
  padding-left: 25px;
  background: url('images/icon-orangelight.png') left center no-repeat;
}

.msg-item-text.warn3 {
  width: 220px;
  padding-left: 25px;
  background: url('images/icon-redlight.png') left center no-repeat;
}

.msg-item-text>img {
  width: 16px;
  height: 16px;
  margin-right: 6px;
  vertical-align: middle;
}

.msg-item-ignore {
  position: absolute;
  top: 50%;
  right: 3px;
  width: 16px;
  height: 12px;
  margin-top: -6px;
  background: url('images/icon-eye.png');
}

.msg-item-ignore:hover {
  background: url('images/icon-eye-hover.png');
}

.msg-head-text {
  padding: 0 10px;
  font-weight: 700;
  color: #666;
  background: #fff;
}

.msg-item-remove {
  position: absolute;
  top: 50%;
  right: 15px;
  display: none;
  width: 15px;
  height: 18px;
  margin-top: -9px;
  cursor: pointer;
  background: url('images/icon-msgdel.png');
}

.msg-item-remove:hover {
  background: url('images/icon-msgdel-hover.png');
}

.msg-panel-srh {
  display: table;
  width: 330px;
  margin: 10px;
}

.msg-srh-input,
.msg-srh-icon {
  display: table-cell;
}

.msg-srh-icon {
  width: 35px;
  background: url(images/search-bg.png);
}

.msg-srh-input {
  width: 288px;
  height: 28px;
  padding-left: 6px;
  font-size: 12px;
  line-height: 28px;
  color: #666;
  border: 1px solid #2a92eb;
  border-left: 0;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  outline: none;
}

.msg-panel-content.search-content {
  top: 88px;
}

.menu-search-history,
.menu-search-result {
  height: 100%;
}

.menu-search-history>.content-head,
.menu-search-result>.content-head {
  margin: 0 10px;
  padding-left: 10px;
  font-size: 14px;
  line-height: 40px;
  color: #0b3a51;
  border-bottom: 1px dashed #ddd;
  background: url('images/msg-titleicon.png') 0 center no-repeat;
}

.menu-search-list {
  position: absolute;
  top: 41px;
  bottom: 0;
  width: 100%;
}

.menu-search-item {
  padding-left: 20px;
  line-height: 36px;
  cursor: pointer;
  color: #3d4b62;
}

.menu-search-item:hover {
  background-color: #dcefff;
}

.menu-search-item>.keyword {
  color: #f73f07;
}

.menu-search-history>.menu-search-list.empty {
  background: url('images/no-history.png') no-repeat 50% 130px;
}

.menu-search-result>.menu-search-list.empty {
  background: url('images/no-result.png') no-repeat 50% 130px;
}


/*E讯*/
.emsg-recent-list.empty {
  background: url('images/no-exun.png') no-repeat 50% 130px;
}
.emsg-recent-item {
  position: relative;
  height: 59px;
  padding: 12px;
  box-sizing: content-box;
  cursor: pointer;
  vertical-align: middle;
  border-bottom: 1px solid #ddd;
}

.emsg-user-img {
  width: 42px;
  height: 42px;
  border-radius: 50%;
}

.emsg-user-img>img {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.emsg-recent-record {
  margin-left: 55px;
}

.emsg-recent-record>h2 {
  line-height: 20px;
}

.emsg-recent-record>h2>.emsg-user-name {
  display: inline-block;
  overflow: hidden;
  max-width: 175px;
  font-size: 14px;
  white-space: nowrap;
  text-overflow: ellipsis;
  -ms-text-overflow: ellipsis;
  color: #0b3a51;
  -o-text-overflow: ellipsis;
}

.emsg-recent-item>.emsg-user-img,
.emsg-dialog-head>.emsg-user-img {
  float: left;
}

.emsg-recent-item .emsg-recent-date {
  float: right;
  font-size: 12px;
  color: #999;
}

.emsg-recent-item.newmsg .emsg-user-img {
  -webkit-animation: notread 1s linear infinite;
  animation: notread 1s linear infinite;
}

@-webkit-keyframes notread {
  0% {
      -webkit-transform: translate(0, 0) rotate(0);
      transform: translate(0, 0) rotate(0);
  }
  10% {
      -webkit-transform: translate(1px, 0) rotate(0);
      transform: translate(1px, 0) rotate(0);
  }
  20% {
      -webkit-transform: translate(-1px, 0) rotate(0);
      transform: translate(-1px, 0) rotate(0);
  }
  30% {
      -webkit-transform: translate(0, -1px) rotate(0);
      transform: translate(0, -1px) rotate(0);
  }
  40% {
      -webkit-transform: translate(0, 1px) rotate(0);
      transform: translate(0, 1px) rotate(0);
  }
  50% {
      -webkit-transform: translate(1px, 0) rotate(0);
      transform: translate(1px, 0) rotate(0);
  }
  60% {
      -webkit-transform: translate(-1px, 0) rotate(0);
      transform: translate(-1px, 0) rotate(0);
  }
  70% {
      -webkit-transform: translate(0, -1px) rotate(0);
      transform: translate(0, -1px) rotate(0);
  }
  80% {
      -webkit-transform: translate(0, 1px) rotate(0);
      transform: translate(0, 1px) rotate(0);
  }
  90% {
      -webkit-transform: translate(1px, 0px) rotate(0);
      transform: translate(1px, 0px) rotate(0);
  }
  100% {
      -webkit-transform: translate(-1px, 0) rotate(0);
      transform: translate(-1px, 0) rotate(0);
  }
}

@keyframes notread {
  0% {
      -webkit-transform: translate(0, 0) rotate(0);
      transform: translate(0, 0) rotate(0);
  }
  10% {
      -webkit-transform: translate(1px, 0) rotate(0);
      transform: translate(1px, 0) rotate(0);
  }
  20% {
      -webkit-transform: translate(-1px, 0) rotate(0);
      transform: translate(-1px, 0) rotate(0);
  }
  30% {
      -webkit-transform: translate(0, -1px) rotate(0);
      transform: translate(0, -1px) rotate(0);
  }
  40% {
      -webkit-transform: translate(0, 1px) rotate(0);
      transform: translate(0, 1px) rotate(0);
  }
  50% {
      -webkit-transform: translate(1px, 0) rotate(0);
      transform: translate(1px, 0) rotate(0);
  }
  60% {
      -webkit-transform: translate(-1px, 0) rotate(0);
      transform: translate(-1px, 0) rotate(0);
  }
  70% {
      -webkit-transform: translate(0, -1px) rotate(0);
      transform: translate(0, -1px) rotate(0);
  }
  80% {
      -webkit-transform: translate(0, 1px) rotate(0);
      transform: translate(0, 1px) rotate(0);
  }
  90% {
      -webkit-transform: translate(1px, 0px) rotate(0);
      transform: translate(1px, 0px) rotate(0);
  }
  100% {
      -webkit-transform: translate(-1px, 0) rotate(0);
      transform: translate(-1px, 0) rotate(0);
  }
}

.emsg-not-read {
  display: inline-block;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #ff3d00;
}

.emsg-recent-record>h2 .emsg-not-read {
  margin: 0 0 6px 7px;
}

.emsg-recent-message {
  overflow: hidden;
  font-size: 13px;
  line-height: 20px;
  white-space: nowrap;
  text-overflow: ellipsis;
  -ms-text-overflow: ellipsis;
  word-break: break-all;
  -ms-word-break: break-all;
  color: #999;
  -o-text-overflow: ellipsis;
}

.emsg-ignore-icon {
  position: absolute;
  right: 19px;
  bottom: 5px;
  display: none;
  width: 40px;
  height: 18px;
  font-size: 12px;
  line-height: 18px;
  cursor: pointer;
  text-align: center;
  color: #fff;
  border-radius: 9px;
  background: #f3c523;
}

.emsg-ignore-icon:hover {
  background: #f96d41;
}

.emsg-recent-item:hover {
  background: #fdf49d;
}

.emsg-recent-item:hover .emsg-ignore-icon {
  display: block;
}
